const UpdangCalendar = ({ events, children, ...rest }) => {
  const calendarRef = useRef();
  const onNextArrowClick = (event, el) => {
    const calendarApi = calendarRef.current.getApi();
    calendarApi.next();
  };
  const onPrevArrowClick = (event, el) => {
    const calendarApi = calendarRef.current.getApi();
    calendarApi.prev();
  };

  const dayHeaderFormatter = (dateObj) => {
    const { dow } = dateObj;
    const DAY = {
      0: '',
      1: '',
      2: '',
      3: '',
      4: '',
      5: '',
      6: '',
    };
    return DAY[dow];
  };
  const setCustomEventContent = (eventInfo) => {
    return (
      <React.Fragment>
        <i>{eventInfo.event.title}</i>
      </React.Fragment>
    );
  };
  const handleDateClick = (arg, b, c) => {};
  const unselectCallback = (unselected) => {
    console.log(['unselected'], unselected);
  };
  const selectedCallback = (selected) => {
    // api    .
    console.log(['selected'], selected);
  };
  const moreLinkClick = (a) => {
    return true;
  };
  // selectLongPressDelay={Number.MIN_VALUE}
  return (
    <UpdangCalendarBlock>
      <FullCalendar
        moreLinkContent={(moreLinkButton) => {
          return <span>...</span>;
        }}
        moreLinkClick={moreLinkClick}
        ref={calendarRef}
        initialView="dayGridMonth"
        plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
        dateClick={handleDateClick}
        selectable={true}
        select={selectedCallback}
        unselect={unselectCallback}
        unselectAuto={true}
        eventContent={setCustomEventContent}
        events={events}
        dayMaxEventRows={true}
        dayHeaderContent={dayHeaderFormatter}
        dayHeaderClassNames={['custom__dayheader']}
        headerToolbar={{
          start: 'PrevNavigationButton',
          center: 'title',
          end: 'NextNavigationButton',
        }}
        titleFormat={(titleProps) => <CustomHeaderTitle {...titleProps} />}
        customButtons={{
          PrevNavigationButton: {
            text: '<',
            hint: 'navigation button to the previous month',
            click: onPrevArrowClick,
          },
          NextNavigationButton: {
            text: '>',
            hint: 'navigation button to the previous month',
            click: onNextArrowClick,
          },
        }}
      />
    </UpdangCalendarBlock>
  );
};

export default UpdangCalendar;
